@use '../abstracts/variables' as var;
@use '../base/mixins' as mixin;

@import './header';
@import './footer';

.section {
  &__title {
    font-family: var.$font-family;
    font-size: 40px;
    font-weight: 700;
    color: #272C38;
    margin-bottom: 70px;

    @media(max-width: 992px) {
      font-size: 36px;
      margin-bottom: 50px;
    }

    @media(max-width: 578px) {
      font-size: 32px;
      margin-bottom: 30px;
    }
  }

  &__description {
    font-family: var.$font-family;
    font-size: 24px;
    font-weight: 300;
    color: #272C38;
    margin-bottom: 140px;
    max-width: 1246px;

    @media(max-width: 992px) {
      font-size: 20px;
      margin-bottom: 100px;
    }

    @media(max-width: 578px) {
      font-size: 16px;
      margin-bottom: 50px;
    }
  }
}

.tabs {
  display: flex;
  gap: 30px;
  margin-bottom: 80px;

  @media(max-width: 992px) {
    margin-bottom: 60px;
  }

  @media(max-width: 578px) {
    margin-bottom: 40px;
  }

  &--wide {
    @media(max-width: 578px) {
      flex-wrap: wrap;
    }
  }

  .tab {
    padding-bottom: 30px;
    font-size: 24px;
    font-weight: 700;
    font-family: var.$font-family;
    border-bottom: 1px solid #818690;
    color: #818690;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 60px;
    cursor: pointer;
    transition: 0.3s linear;

    @media(max-width: 578px) {
      flex-grow: 1;
    }

    &--active {
      border-bottom: 1px solid #272C38;
      color: #272C38;
    }

    &__text {
      font-size: 18px;
    }

    svg {
      display: block;
      margin-top: -5px;

      path {
        transition: 0.3s linear;
        stroke: #818690;
      }
    }

    &--active svg path {
      stroke: #272C38;
    }
  }

  &--wide .tab {
    @media(max-width: 578px) {
      width: 100%;
    }
  }
}

.tab-body {
  display: none;

  &--active {
    display: block;
  }
}

.custom-select {
  position: relative;

  &__input {
    position: relative;
    cursor: pointer;

    input {
      pointer-events: none;
    }
  }

  &__caret {
    position: absolute;
    top: 19px;
    right: 20px;
    transform-origin: center;
    transition: 0.3s linear;
  }

  &__options {
    position: absolute !important;
    top: 110%;
    width: 100%;
    max-height: 150px;
    overflow-x: hidden;
    overflow-y: auto;
    background: #E4E4E4;
    font-family: var.$font-family;
    padding: 0 1px;
    font-size: 16px;
    font-weight: 400;
    color: #272C38;
    box-shadow: 0 10px 15px 0 rgba(0, 0, 0, 0.2);
    height: 0;
    opacity: 0;
  }

  &--active &__options {
    height: 150px;
    opacity: 1;
  }

  &__option {
    position: relative;
    display: block;

    input {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      pointer-events: none;
      width: 100%;
      height: 100%;

      &:checked+.custom-select__label {
        background: #fff;
      }
    }
  }

  &__label {
    padding: 15px 17px;
    box-sizing: border-box;
    transition: 0.3s linear;
    cursor: pointer;

    &:hover {
      background: #fff;
    }
  }
}

.border-line {
  position: relative;
  border-top: 1px solid transparent;
  border-bottom: 1px solid #272C38;

  &::before,
  &::after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    width: 1px;
    background: linear-gradient(to bottom, #C9C7C8, #272C38);
  }

  &::before {
    left: 0;
  }

  &::after {
    right: 0;
  }

  &--light {
    border-top: 1px solid transparent;

    &::before,
    &::after {
      background: linear-gradient(to bottom, rgba(203, 201, 202, 0) 30%, rgba(39, 44, 56, 0.6));
    }
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  justify-content: center;
  align-items: center;
  display: none;

  &::-webkit-scrollbar {
    width: 5px;
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background: #272C38;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: #272C38;
  }

  @media(max-width: 712px) {
    align-items: flex-start;
    overflow-y: auto;
    max-height: 100%;
  }

  &--active {
    display: flex;
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
  }

  &__inner {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end;
    padding: 0 26.55px;
    width: 90%;
    max-width: 1246px;
    max-height: 85%;
    background: url('@images/modal__banner.png') no-repeat;
    background-size: cover;

    @media(max-width: 992px) {
      width: 100%;
      min-height: 100%;
      align-items: center;
      padding-top: 25px;
    }

    @media(max-width: 712px) {
      max-height: unset;
    }
  }

  &__btn {
    margin-top: 45px;
    margin-right: 19px;
    margin-bottom: 45px;
    background: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    z-index: 10;
    width: 24px;
    height: 24px;

    @media(max-width: 992px) {
      margin-right: 0;
    }

    svg {
      width: 100%;
      height: 100%;

      path {
        stroke: #fff;
      }
    }
  }

  &__form {
    width: 90%;
    overflow: hidden;
    overflow-y: auto;
    max-width: 482px;

    &::-webkit-scrollbar {
      width: 5px;
    }

    &::-webkit-scrollbar-track {
      background: transparent;
    }

    &::-webkit-scrollbar-thumb {
      background: #272C38;
    }

    &::-webkit-scrollbar-thumb:hover {
      background: #272C38;
    }

    @media(max-width: 712px) {
      width: 100%;
    }
  }
}

.form-group {
  margin-bottom: 14.96px;

  &__label {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.38;
    color: #C1C1C1;
    margin-bottom: 12.96px;
  }

  &__input {

    input,
    textarea {
      width: 100%;
      resize: none;
      background: #D3D3D3;
      font-family: var.$font-family;
      font-size: 20px;
      font-weight: 400;
      color: #606060;
      padding: 13.96px 22px;
      border: none;
      outline: none;

      @media(max-width: 712px) {
        padding: 10px 22px;
      }

      &::placeholder {
        font-family: var.$font-family;
        font-size: 20px;
        font-weight: 400;
        color: #606060;
      }
    }

    textarea {
      min-height: 133px;
    }
  }
}

.custom-btn {
  position: relative;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0;
  background: transparent;

  &--in {
    width: 100%;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 31px 20px;
    font-size: 16px;
    font-weight: 300;
    color: #fff;
    text-transform: uppercase;
    line-height: 1.2;
    background: radial-gradient(circle, #778190, #4C5664);
    display: flex;
    align-items: center;
    justify-content: center;
  }

  svg {
    margin-top: -5px;
  }

  path {
    stroke: #fff;
  }
}

.form {
  &__btn {
    padding: 7px;
    border: 1px solid #566170;
    background: #D9D9D9;
    display: block;
    margin: 0 auto 45px;
  }

  .custom-btn--in {
    color: #C1C1C1;

    svg {
      margin-top: -3px;
    }
    
    path {
      stroke: #282D36;
    }
  }
}