.header {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 5;
  padding: 0 20px;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.05));

  &__burger {
    overflow: hidden;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    height: 100vh;
    z-index: 100;
    background-color: #D4D4D4;
    transform: translate(0, -100%);
    transition: all .3s ease-in-out;
    overflow-y: auto;

    &--wrapper {
      position: relative;
      height: 100%;
      width: 100%;
      padding: 52px 30px 0 30px;
      box-sizing: border-box;
    }

    &--image {
      img {
        object-fit: cover;
      }

      &:nth-child(2) {
        align-self: flex-end;
      }
    }

    ul {
      li {
        font-size: 36px;
        font-weight: 300;

        &:not(:last-child) {
          margin-bottom: 20px;
        }

        a {
          font-size: 36px;
          font-weight: 300;
          text-decoration: none;
          &:visited {
            color: #272C38;
          }
        }
      }
    }

    .header__socials {
      display: flex;

      svg {
        path {
          fill: black;
        }
      }
    }

    &--top {
      display: flex;
      justify-content: space-between;
      margin-bottom: 40px;

      svg {
        cursor: pointer;
      }
    }

    &--active {
      transform: translate(0, 0);
    }

    &--icon {
      margin-left: 24px;
      cursor: pointer;
      display: none;

      @media(max-width:992px) {
        display: block;
      }
    }
  }

  &__images {
    margin-top: 59.5px;
    display: flex;
    flex-direction: column;
    gap: 17.14px;
  }

  &__nav {
    ul {
      display: flex;
      align-items: center;

      @media(max-width:992px) {
        display: none;
      }

      li {
        font-size: 16px;
        font-weight: 300;
        cursor: pointer;
        text-transform: uppercase;
        text-decoration: none;
        color: white;

        &:not(:last-child) {
          margin-right: 40px;

          @media(max-width:1480px) {
            margin-right: 20px;
          }
        }

        a {
          text-decoration: none;
          color: white;

          &:visited {
            color: white;
          }
        }
      }
    }
  }

  &__locale {
    position: relative;
    font-size: 16px;
    color: white;

    &--current {
      display: flex;
      align-items: center;
      cursor: pointer;
      text-transform: capitalize;

      svg {
        margin-left: 10px;
      }
    }

    &--body {
      width: fit-content;
      position: absolute;
      background-color: white;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 10px 15px;
      box-shadow: 0 18px 30px rgba(0, 0, 0, .1);
      padding: 20px;
      display: none;

      &.active {
        display: block;
      }

      ul {
        li {
          cursor: pointer;
          color: black;

          &:not(:last-child) {
            margin-bottom: 5px;
          }
        }
      }
    }
  }

  &__socials {
    display: flex;
    align-items: center;
    margin-right: 68px;

    @media(max-width:992px) {
      display: none;
    }

    @media(max-width:1280px) {
      margin-right: 30px;
    }

    a {
      display: block;

      &:not(:last-child) {
        margin-right: 40px;

        @media(max-width:1280px) {
          margin-right: 20px;
        }
      }
    }
  }

  &__right {
    display: flex;
    align-items: center;
  }

  &__left {
    display: flex;
    align-items: center;
  }

  &__logo {
    cursor: pointer;
    margin-right: 95px;

    @media(max-width:1280px) {
      margin-right: 40px;
    }
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 31px 0 42px 0;
    border-bottom: 1px rgba(255, 255, 255, 0.19) solid;
  }
}