@use '../abstracts/variables' as var;
@use '../base/mixins' as mixin;

html {
  width: 100%;
}

body {
  font-family: var.$font-family;
  color: var.$dark;
  position: relative;
  width: 100%;
  overflow-x: hidden;
  min-height: 100vh;
  background: url('@images/pattern-removebg-preview.png') repeat-x 0 100%;
  background-color: #D4D4D4;
  background-size: unset 30%;
}

.container {
  max-width: 1600px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;

  @media(max-width:1600px) {
    max-width: 1480px;
  }

  @media(max-width:1480px) {
    max-width: 1280px;
  }

  @media(max-width:992px) {
    padding: 0 30px;
  }

  @media(max-width:992px) {
    padding: 0 30px;
  }

  @media(max-width:440px) {
    padding: 0 15px;
  }
}

section {
  z-index: 2;
  position: relative;
}

.lines {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  z-index: 2;
  pointer-events: none;

  span {
    width: 1px;
    background-color: rgba(0, 0, 0, 0.015);
  }
}

.heading {
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 70px;

  @media(max-width:1280px) {
    margin-top: 50px;
    font-size: 32px;
  }

  @media(max-width:992px) {
    margin-bottom: 30px;
  }
}

.btn__wrapper {
  display: inline-block;
  padding: 0;
  border: none;
}

.btn {
  display: inline-flex;
  cursor: pointer;
  padding: 30px 20px;
  text-transform: uppercase;
  align-items: center;
  gap: 10px;
  backdrop-filter: blur(3px);
  color: white;
  background: radial-gradient(50% 50% at 50% 50%, rgb(119, 129, 144) 0%, rgb(117, 128, 143) 0.009999999747378752%, rgb(117, 127, 143) 0.019999999494757503%, rgb(76, 86, 100) 100%);
  text-decoration: none;

  @media(max-width:480px) {
    padding: 15px;
    font-size: 14px !important;
  }

  @media(max-width:430px) {
    padding: 10px;
  }
}