@use '../abstracts/variables' as var;
@use '../base/mixins' as mixin;

$font-family: 'Helvetica', sans-serif !default;
$font-family2: 'Geologica', sans-serif !default;

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 16px;

}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-size: inherit;
  font-family: var.$font-family;
}

input,
button {
  font-size: inherit;
}

ul,
li {
  list-style: none;
}

button {
  outline: none;
  border: 0;
}