@use '../abstracts/variables' as var;

.footer {
  padding: 45px 0;
  border-bottom: 1px rgba(255, 255, 255, 0.19) solid;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1100px;
    margin: 0 auto;

    @media(max-width:380px) {
      flex-direction: column;
      align-items: center;
      gap: 15px;
    }
  }

  &__logo {
    cursor: pointer;
  }

  &__nav {
    ul {
      flex-wrap: wrap;

      @media(max-width:600px) {
        gap: 15px;
        justify-content: center;
      }

      li:not(:last-child) {
        @media(max-width:600px) {
          margin-right: 0 !important;
        }
      }
    }

    @media(max-width:992px) {
      display: none;
    }

    &--hidden {
      display: none;

      @media(max-width:992px) {
        display: flex;
      }
    }
  }

  &__socials {
    display: flex;
    align-items: center;
    margin-right: 68px;

    @media(max-width:380px) {
      margin-right: 0;
    }

    svg {
      path {
        fill: var.$dark;
      }
    }

    @media(max-width:1280px) {
      margin-right: 30px;
    }

    a {
      display: block;

      &:not(:last-child) {
        margin-right: 40px;

        @media(max-width:1280px) {
          margin-right: 20px;
        }
      }
    }
  }

  ul {
    display: flex;
    align-items: center;

    li {
      color: var.$dark;
      font-size: 16px;
      font-weight: 300;
      cursor: pointer;
      text-transform: uppercase;

      &:not(:last-child) {
        margin-right: 40px;

        @media(max-width:1480px) {
          margin-right: 20px;
        }
      }

      a {
        color: var.$dark;
        font-size: 16px;
        font-weight: 300;
        text-decoration: none;

        &:visited {
          color: var.$dark;
        }
      }
    }
  }

  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 60px;

    @media(max-width:992px) {
      flex-direction: column;
      gap: 30px;
    }
  }
}